import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";

const routes: Routes = [
  {
    path: "",
    loadChildren: () =>
      import("./components/pages/home-two/home-two.module").then(
        (m) => m.HomeTwoModule
      ),
    data: { breadcrumb: "Inicio" },
  },
  // Services
  {
    path: "servicios",
    loadChildren: () =>
      import("./components/pages/services/services.module").then(
        (m) => m.ServicesModule
      ),
    data: { breadcrumb: "Servicios" },
  },
  {
    path: "servicio/:id",
    loadChildren: () =>
      import("./components/pages/service-details/service-details.module").then(
        (m) => m.ServiceDetailsModule
      ),
    data: { breadcrumb: "Service Details" },
  },
  // Doctors
  {
    path: "sobre-mi",
    loadChildren: () =>
      import("./components/pages/doctor-details/doctor-details.module").then(
        (m) => m.DoctorDetailsModule
      ),
    data: { breadcrumb: "Sobre Mi" },
  },
  // Contact
  {
    path: "contacto",
    loadChildren: () =>
      import("./components/pages/contact/contact.module").then(
        (m) => m.ContactModule
      ),
    data: { breadcrumb: "Contacto" },
  },
  // Extra
  {
    path: "error-page",
    loadChildren: () =>
      import("./components/pages/error-page/error-page.module").then(
        (m) => m.ErrorPageModule
      ),
    data: { breadcrumb: "Error 404" },
  },
  {
    path: "**",
    loadChildren: () =>
      import("./components/pages/error-page/error-page.module").then(
        (m) => m.ErrorPageModule
      ),
    data: { breadcrumb: "Error 404" },
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule],
})
export class AppRoutingModule {}
